import {
  Injectable,
  Inject
} from '@angular/core';
import {
  Http,
  Response
} from '@angular/http';
import {
  WebApiUrl
} from '../constants/constant';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class SharedService {
  constructor(private http: Http) { }

  getData() {
    return this
      .http
      .get(WebApiUrl.apiUrl)
      .map((res: Response) => {
        return res.json();
      }).toPromise();
  }
}



