import { Employee } from './models/employee.model';
import { SharedService } from './services/shared.service';
import { Component } from '@angular/core';
import { WOW } from 'wowjs/dist/wow';
import { DataBase } from './models/database.model';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  isNavbarVisible: boolean;
  isViewMoreActive: boolean;
  showViewBtn: boolean;
  showCareerBtn: boolean;
  showCareer: boolean;
  database: DataBase;
  ceo: Employee;
  currentYear: string;
  tempEmpList: Array<Employee>;
  constructor(private sharedService: SharedService) {
  }


  ngAfterViewInit() {
    new WOW().init();
  }

  async ngOnInit() {
    this.isViewMoreActive = true;
    this.showViewBtn = false;
    this.showCareerBtn = false;
    this.showCareer = false;
    this.tempEmpList = new Array<Employee>();
    await this.getDetails();
  }

  async getDetails() {
    let data = await this.sharedService.getData();
    if (data) {
      this.database = data;
      let counter = 300;
      this.database.company.services.forEach((service, index) => {
        if (index === 0) {
          service.delay = 0 + 'ms';
        } else {
          service.delay = counter + 'ms';
          counter += 300;
        }

      });
      let ceo = this.database.employees.find(e => e.employeeId === 1001);
      if (ceo) {
        this.ceo = ceo;
      }
      console.log(data);
      this.currentYear = new Date().getUTCFullYear().toString();

      this.showViewBtn = (this.database && this.database.employees.length > 4);
      this.showCareerBtn = (this.database && this.database.careers.length > 0);

      if (this.showViewBtn) {
        this.tempEmpList = (this.database.employees.length === 4) ?
          this.database.employees.slice(4)
          : this.database.employees.slice(4, this.database.employees.length);
        this.database.employees.forEach(emp => {
          if (this.tempEmpList.some(e => e.employeeId === emp.employeeId)) {
            emp.displayOnHome = false;
          }
          else {
            emp.displayOnHome = true;
          }
        });
      } else {
        this.database.employees.forEach(emp => {
          emp.displayOnHome = true;
        });
      }
    }
  }

  setIsViewMoreActive() {
    if (this.showViewBtn) {

      if (this.isViewMoreActive) {
        this.setDisplayOnHomeForEmpList(true);
      } else {
        this.setDisplayOnHomeForEmpList(false);
      }

      this.isViewMoreActive = !this.isViewMoreActive;
    }
  }

  toggleCareers() {
    this.showCareer = !this.showCareer;
  }

  toggleFromMenuCareers() {
    this.showCareer = !this.showCareer;
    this.isNavbarVisible = !this.isNavbarVisible;
  }

  private setDisplayOnHomeForEmpList(displayOnHome: boolean) {
    this.database.employees.forEach(emp => {
      if (this.tempEmpList.some(e => e.employeeId === emp.employeeId)) {
        emp.displayOnHome = displayOnHome;
      }
    });
  }
}



